body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}


.header-color {
  background: #833ab4;  /* fallback for old browsers */
  background: -webkit-linear-gradient(to top, #fcb045, #fd1d1d, #833ab4);  /* Chrome 10-25, Safari 5.1-6 */
  background: -webkit-linear-gradient(bottom, #fcb045, #fd1d1d, #833ab4);
  background: linear-gradient(to top, #fcb045, #fd1d1d, #833ab4); /* W3C, IE 10+/ Edge, Firefox 16+, Chrome 26+, Opera 12+, Safari 7+ */
  height: 200px;
}
.landing-grid {
  background: #00bf8f;  /* fallback for old browsers */
  background: -webkit-linear-gradient(to left, #001510, #00bf8f);  /* Chrome 10-25, Safari 5.1-6 */
  background: -webkit-linear-gradient(right, #001510, #00bf8f);
  background: linear-gradient(to left, #001510, #00bf8f); /* W3C, IE 10+/ Edge, Firefox 16+, Chrome 26+, Opera 12+, Safari 7+ */


  text-align: center;
}
.avatar-img{
  height: 250px;
  padding-top: 5em;
}
.banner-text {
  background-color: black;
  opacity: .8;
  width: 75%;
  margin: auto;
  border-radius: 10px;
}

.banner-text h1 {
  font-size: 66px;
  font-weight: bold;
  color: white;

}
.banner-text h1 text{
  font-size: 66px;
  font-weight: bold;
  color: white;
alignment: center;
}
.banner-text hr{
  border-top: 5px dotted white;
  width: 50%;
  margin: auto;
}
.banner-text p{
  color: white;
  font-size: 20px;
  padding: 1em;
}
.social-links {
color: white;
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: space-between;
          justify-content: space-between;
  width: 50%;
  margin: auto;
}
.social-links i{
  color: white;
  font-size: 5em;
  padding: 8px;
}

.contact-body{
/*background: gray;*/
  color: black;
  width: 100%;
  height: 100%;
  padding-top: 2em;
  -webkit-justify-content: center;
          justify-content: center;
  margin: auto;
  position: fixed;
}
.contact-grid{
text-align: center;
  -webkit-justify-content: center;
          justify-content: center;
  width: 80%;
  height: 40px;
  background: white;
  /*border: 2px solid red;*/
  margin-bottom: 20px;
}
.contact-grid h2{
  font-family: 'Ubuntu', sans-serif;
}
.contact-grid p{
  font-family: 'Josefin Sans', sans-serif;
  font-weight: bold;
}
.contact-grid hr{
  border-top: 5px dashed black;
  width: 50%;
  margin: auto;
}
.contact-list i{
  font-size: 66px;
  padding-right: 5rem;

}
.projects-grid{
  display: -webkit-flex;
  display: flex;
}

